.Container-Mobile-Order {
  align-items: flex-start;
  padding: 20px 15px;
}

.Order-Number-Title {
  display: flex;
  flex-direction: row;
  text-align: left;
  font-size: 32px;
  line-height: 22px;
  font-family: "SFPro-Semibold";
  color: #404040;
}

.Order-Number-Payment-Title {
  display: flex;
  flex-direction: row;
  font-family: "SFPro-Semibold";
  text-align: left;
  font-size: 30px;
  line-height: 28px;
  color: #404040;
  margin-left: 8px;
}
