.Order-DeliveryRow-Container {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1;
  margin-bottom: 15px;
}

.Order-DeliveryRow-Value {
  font-size: 16px;
  line-height: 28px;
  font-family: "SFPro-Semibold";
  color: #404040;
  margin-left: 15px;
}

.Order-DeliveryRow-Press {
  cursor: pointer;
}

.Order-DeliveryRow-Underline {
  text-decoration: underline;
}
