.Order-Row-Container {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.Order-Row-Title {
  font-size: 16px;
  line-height: 28px;
  font-family: "SFPro-Semibold";
  color: #8b8b8b;
}

.Order-Row-Value {
  font-size: 16px;
  line-height: 28px;
  font-family: "SFPro-Semibold";
  color: #404040;
}

.Order-Row-Value-Purple {
  font-size: 16px;
  line-height: 28px;
  font-family: "SFPro-Heavy";
  font-weight: 900;
  color: #fe036a;
}

.text-align-left- {
