.Container-Product {
  display: flex;
  flex-direction: row;
  padding-top: 20px;
  margin-top: 20px;
  margin-right: 5px;
  border-top: 1px solid #ebebeb;
}

.Image-Container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  margin-right: 8px;
  background-color: white;
  border-radius: 12px;
}

.Product-Content-Container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.Product-Content-Title {
  font-size: calc(1em + 0.3vw);
  line-height: 16px;
  font-family: "SFPro-Semibold";
  text-align: left;
  color: #404040;
}

.Product-Content-Weight {
  font-size: 11px;
  line-height: 13px;
  font-family: "SFPro-Semibold";
  color: #8b8b8b;
}

.Product-Content-Price {
  margin-top: auto;
  margin-left: auto;
  font-size: 18px;
  line-height: 22px;
  font-family: "SFPro-Bold";
  color: #404040;
}

.Image-Product {
  height: calc(1em + 10vw);
  width: 100%;
  max-width: calc(1em + 10vw);
  padding: 7px;
  background-color: white;
  border-radius: 12px;
  margin-right: 8px;
  object-fit: contain;
}
